<template>
  <div class="container">
    <div class="text-center" id="slogan">
      <h1 class="mt-5">NameGator</h1>
      <h6 class="text-secondary mt-2 mb-3">
        Gerador de nome utilizando <b> Vue.js, GraphQL e Node</b>
      </h6>
    </div>

    <div id="main">
      <div class="container">
        <div class="row">
          <div class="col-md">
            <h5 class="mt-1">
              Prefixos
              <span class="badge badge-info">
                {{prefixos.length}}
              </span>
            </h5>
            <div class="card">
              <div class="card-body">
                <ul class="list-group">
                  <li class="list-group-item" v-for="prefix in prefixos" v-bind:key="prefix">
                    {{prefix}}
                  </li>
                </ul>
                <br>
                <div class="input-group">
                  <input type="text" placeholder="Digite o prefixo" v-model="prefix" class="form-control">
                  <div class="input-group-append">
                    <button class="btn btn-info" v-on:click="adicionaPrefix(prefix)"><span class="fa fa-plus"></span></button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md">
            <h5 class="mt-1">
              Sufixos
              <span class="badge badge-info">
                {{prefixos.length}}
              </span>  
            </h5>
            <div class="card">
              <div class="card-body">
                <ul class="list-group">
                  <li class="list-group-item" v-for="sufix in sufixos" v-bind:key="sufix">
                    {{sufix}}
                  </li>
                </ul>
                <br>
                <div class="input-group">
                  <input type="text" placeholder="Digite o sufixo" v-model="sufix" class="form-control">
                  <div class="input-group-append">
                    <button class="btn btn-info" v-on:click="adicionaSufix(sufix)"><span class="fa fa-plus"></span></button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <hr>

        <h5>
          Domínios
          <span class="badge badge-info">{{dominios.length}}</span>
        </h5>
        <div class="card">
          <div class="card-body">
            <ul class="list-group">
              <li class="list-group-item" v-for="domain in dominios" v-bind:key="domain">
                {{domain}}
              </li>
            </ul>
          </div>
        </div>

      </div>
    </div>
  </div>
 
</template>

<script>
import "bootstrap/dist/css/bootstrap.css"
import "font-awesome/css/font-awesome.css"

export default {
  name: 'app',
  data: ()=>{
    return{
      prefix : "",
      sufix: "",
      prefixos: ['Prometeus', 'Tiwanakos', 'MachuPichu', 'Udia'],
      sufixos: ['Tour', 'Travel', 'Executive', 'Enterprise'],
      dominios: []
    }
  },

  methods: {
    adicionaPrefix(prefix){
      this.prefixos.push(prefix)
      this.prefix = ""
      this.gerarDominios()
    },

    adicionaSufix(sufix){
      this.sufixos.push(sufix)
      this.sufix = ""
      this.gerarDominios()
    },

    gerarDominios(){
      this.dominios = []
      for(const prefix of this.prefixos){
        for(const sufix of this.sufixos){
          this.dominios.push(prefix + sufix)
        }
      }
    }
  }
}
</script>


<style>

#main{
  background: #f1f1f1;
  padding-top: 30px;
  padding-bottom: 30px;
  border-radius: 15px;
}

</style>
